.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    background-color: white;
    z-index: 100;
    width: 100%;
}

.header__icon {
    object-fit: contain;
    height: 100px;
    margin-left: 20px;
}

.header__center {
    display: flex;
    align-items: center;
    flex: 1;
    max-width: fit-content;
    padding: 10px;
    height: 30px;
    border: 1px solid lightgray;
    border-radius: 999px;
}

.header__center > input {
    border: none;
    padding: 10px;
    outline-width: 0ch;
}
.header__right {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 15wv;
    margin-right: 80px;
}

.header__right > p {
    padding: 15px;
}

.headerRight__languageIcon {
    padding: 10px;
}

.headerRight__expandMoreIcon {
    padding-left: 10px;
    padding-right: 5px;
}

@media (max-width: 610px) {
    .header__right {
        display: none;
    }
    .header__icon {
        display: none;
    }

    .header__center {
        position: relative;
        max-width: stretch;
        margin: 30px;
        text-align: center;
    }

    .header__searchIcon {
        position: absolute;
        align-items: left;
    }

    .header__center > input {
        text-align: left;
        margin-left: 20px;
        width: 100%;
    }
}